import React from 'react';
import GuardedContainer from '../components/GuardedContainer';
import FormElement from '../components/FormElement';
import PhoneNumber from '../components/PhoneNumber';
import AppComponent from '../components/AppComponent';
import {Row, Button} from 'react-bootstrap';
import {getIndustrySegments, getBusinessSegments} from '../services/MasterData';
import {registerManufacturer} from '../services/registration';
import {FormGroup,ControlLabel,FormControl, Alert} from 'react-bootstrap';
import {isValidForm, highlightError} from '../components/utilities';
import {validateEmail, validateGST, validatePincode, validateWebsite, validateMobileNumber} from '../utils/validator';

class Register extends AppComponent {

  constructor(props) {
    super(props);
    this.state = {};
    this.inputs = {};
    this.initState();
    this.handleSubmit = this.handleSubmit.bind(this);
    this.setError = this.setError.bind(this);
  }

  async initState() {
    const industrySegments = await getIndustrySegments();
    const businessSegments = await getBusinessSegments();
    this.setState({
      'industrySegments': industrySegments,
      'businessSegments': businessSegments
    });
  }

  setError(errorMessage) {
    this.setState({hasError:true,errorMsg:errorMessage});
  }

  validate() {
    if (isValidForm('registrationForm')) {
      var isValid = true;
      if (validateEmail(this.state.inputs.emailId) === false) {
        highlightError('emailId');
        isValid = false;
      }
      if (validateGST(this.state.inputs.gstNo) === false) {
        highlightError('gstNo');
        isValid = false;
      }
      if (validatePincode(this.state.inputs.pincode) === false) {
        highlightError('pincode');
        isValid = false;
      }
      if (validateWebsite(this.state.inputs.website) === false) {
        highlightError('website');
        isValid = false;
      }
      if (validateMobileNumber(this.state.inputs.mobileNumber) === false) {
        highlightError('mobileNumber');
        isValid = false;
      }
      return isValid;
    }
    return true;
  }

  async handleSubmit() {
    this.setState({hasError:false});
    if(isValidForm('registrationForm') && this.validate()) {
        registerManufacturer(this.inputs).then(res => {
          if(res.status === 409) {
            res.json().then(responseBody => {
              let errCode = responseBody.errorCode;
              if(errCode === "ERR_USR_EXST") {
                this.setError('User already exists.')
              } else if (errCode === "ERR_ORG_EXST") {
                this.setError('Organisation already exists.')
              }
            });
          } else if (res.status !== 200) {
            // show general error
            this.setError('Technical issue. Please contact system admin.')
          } else if (res.status === 200) {
            this.setError('User Registered');
          }
        });
    } else {
      this.setError('Please fill in the highlighted fields.');
    }
  }

  render() {
    const industrySegments = this.state.industrySegments && Object.keys(this.state.industrySegments).map((opt,index) =>
      <option key={this.state.industrySegments[opt].id} value={this.state.industrySegments[opt].id}>{this.state.industrySegments[opt].name}</option>
    );
    const businessSegments = this.state.businessSegments && Object.keys(this.state.businessSegments).map((opt,index) =>
      <option key={this.state.businessSegments[opt].id} value={this.state.businessSegments[opt].id}>{this.state.businessSegments[opt].name}</option>
    );

    return(
        <GuardedContainer pageHeader="Manufacturer Registration">
        {this.state.hasError &&
          <Alert bsStyle="danger">
            {this.state.errorMsg}
          </Alert>}
          <form id="registrationForm">
          <Row>
          <FormElement id="userId" labelText="User ID" type="text" name="userId" onChange={this.handleInputChange} required="true"/>
          <FormElement id="password" labelText="password" type="password" name="password" onChange={this.handleInputChange} required="true"/>
          </Row>
          <Row>
          <FormElement id="orgName" labelText="Name of the organization" type="text" name="orgName" onChange={this.handleInputChange} required="true"/>
          <FormElement id="gstNo" labelText="GST No" type="text" name="gstNo" onChange={this.handleInputChange} required="true"/>
          </Row>
          <Row>
          <FormElement id="address" labelText="Address" type="text" name="address" onChange={this.handleInputChange} required="true"/>
          <div className="col-md-6">&nbsp;</div>
          </Row>
          <Row>
          <FormElement id="city" labelText="City/Town" type="text" name="city" onChange={this.handleInputChange} required="true"/>
          <FormElement id="pincode" labelText="Pincode" type="number" name="pincode" onChange={this.handleInputChange} required="true"/>
          </Row>
          <Row>
          <FormElement id="state" labelText="State" type="text" name="state" onChange={this.handleInputChange} required="true"/>
          <PhoneNumber onChange={this.handleInputChange} required="true"/>
          </Row>
          <Row>
          <FormElement id="faxNumber" labelText="Fax No" type="number" name="faxNumber" onChange={this.handleInputChange} required="true"/>
          <FormElement id="website" labelText="Website" type="text" name="website" onChange={this.handleInputChange} required="true"/>
          </Row>
          <Row>
          <FormGroup className="col-md-6">
            <ControlLabel>Industry Segment</ControlLabel>
            <FormControl componentClass="select" id="industrySegment" name="industrySegment" onChange={this.handleInputChange} mandatory="true">
                <option key='0' value=""> --select one--</option>
                {industrySegments}
            </FormControl>
          </FormGroup>
          <FormGroup className="col-md-6">
            <ControlLabel>Business Segment</ControlLabel>
            <FormControl componentClass="select" id="businessSegment" name="businessSegment" onChange={this.handleInputChange} mandatory="true">
                <option key='0' value=""> --select one--</option>
                {businessSegments}
            </FormControl>
          </FormGroup>
          </Row>
          <Row>
          <FormElement id="contactPerson" labelText="Contact Person" type="text" name="contactPerson" onChange={this.handleInputChange} required="true"/>
          <FormElement id="designation" labelText="Designation" type="text" name="designation" onChange={this.handleInputChange} required="true"/>
          </Row>
          <Row>
          <FormElement id="department" labelText="Department" type="text" name="department" onChange={this.handleInputChange} required="true"/>
          </Row>
          <Row>
          <FormElement id="mobileNumber" labelText="Mobile No." type="number" name="mobileNumber" onChange={this.handleInputChange} required="true"/>
          <FormElement id="emailId" labelText="E-Mail ID" type="text" name="emailId" onChange={this.handleInputChange} required="true"/>
          </Row>
          <Button bsStyle="info" onClick={this.handleSubmit} >Register</Button>
          </form>
        </GuardedContainer>
    );

  }
}

export default Register;



