import React, {Component} from 'react';

class Header extends Component {
	render() {
		return(
				<div className="col-md-12 header">
		    	<div className="col-md-1"></div>
					<div className="col-md-2">
						<img alt="logo" src="img/headerlogo.png" className="headerLogo"></img>
					</div>
					<div className="col-md-2" >
						<div style={{'fontSize': 'smaller'}}>Powered by CircumFusion</div>
    			</div>
					<div className="col-md-4"></div>
					{ this.props.isGuarded &&
					<div className="col-md-2" >
						<i className="fa fa-user-circle" aria-hidden="true" style={{'color': '#9d9d9d','fontSize': '31px'}}></i>
						<span style={{'fontSize': 'small','fontWeight': 'bolder','marginLeft':'10px'}}>Hi UserName </span>
	          <i className="fa fa-chevron-down" aria-hidden="true" style={{'fontSize': 'smaller !important','color': '#635f5f'}}></i>
					</div>
					}
          <div className="col-md-1"></div>
      	</div>
			);
	}
}

export default Header;



